import React, {Component} from 'react';
import {Container, Card, CardImg, CardText, CardBody, CardTitle, CardSubtitle} from 'reactstrap';
import {CSSTransition, TransitionGroup} from 'react-transition-group';
import {connect} from 'react-redux';
import {getItems} from '../actions/itemAction';
import PropTypes from 'prop-types';


class Products extends Component {
  componentDidMount() {
    this.props.getItems();
  }

  onDeleteClick = id => {
    this.props.deleteItem(id);
  };

  render() {
    const { items } = this.props.item;
    return (
      <Container>
          <TransitionGroup className="product-list">
            
            {items.map(({ _id, name, description, price, category, color, image }) => (
              <CSSTransition key={_id} timeout={500} classNames="fade">
                
                <Card className="card-margin">
                  <CardImg top width="2%" src={image} alt={name} />
                  <CardBody>
                    <CardTitle><h3>{name}</h3></CardTitle>
                    <CardSubtitle>{category}</CardSubtitle>
                    <CardText>Color: {color} <br/> Price: {price} <br/> Description: {description}</CardText>
                  </CardBody>
                </Card>

              </CSSTransition>
            ))}

          </TransitionGroup>
      </Container>
    );
  }
}

Products.propTypes = {
	getItems: PropTypes.func.isRequired,
	item:  PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
	item: state.item
});

export default connect(mapStateToProps, {getItems})(Products);