import React, {Component} from 'react';
import {Button, Modal, ModalHeader,  ModalBody, Form, FormGroup, Label, Input, Row, Col} from 'reactstrap';
import {connect} from 'react-redux';
import {addItem} from '../actions/itemAction';

class ItemModal extends Component {
	state = {
		modal: false,
		name: ''
	}

	toggle = () => {
		this.setState({
			modal: !this.state.modal
		});
	};

	onChange = (e) => {
		this.setState({ [e.target.name] : e.target.value });
	};

	onSubmit = (e) => {
		e.preventDefault();
		const newItem = {
			name: this.state.name,
			color: this.state.color,
			price: this.state.price,
			description: this.state.description,
			category: this.state.category,
			image: this.state.image
		}
		// console.log(newItem);
		this.props.addItem(newItem);
		this.toggle();
	};



	render(){
		return(
			<div>
				<Button 
					color='dark'
					className="add-btn"
					onClick={this.toggle}
				>Add Product</Button>

				<Modal isOpen={this.state.modal} toggle={this.toggle}>
					<ModalHeader toggle={this.toggle}>Add to product list</ModalHeader>
					<ModalBody>
						
						<Form onSubmit={this.onSubmit}>
							<FormGroup>
							<Row>
								<Col xs="6">
									<Label for="name">Name</Label>
									<Input type="text" name="name" placeholder="Name" onChange={this.onChange} className="input-margin" />
								</Col>

								<Col xs="6">
									<Label for="color">Color</Label>
									<Input type="text" name="color" placeholder="Color" onChange={this.onChange} className="input-margin" />
								</Col>
							</Row>

							<Row>

								<Col xs="6">
									<Label for="price">Price</Label>
									<Input type="text" name="price" placeholder="Price" onChange={this.onChange} className="input-margin" />
								</Col>

								<Col xs="6">
									<Label for="description">Description</Label>
									<Input type="text" name="description" placeholder="Description" onChange={this.onChange} className="input-margin" />
								</Col>
							</Row>

							<Row>
								<Col xs="6">
									<Label for="category">Category</Label>
									<Input type="text" name="category" placeholder="Category" onChange={this.onChange} className="input-margin" />
								</Col>

								<Col xs="6">
									<Label for="image">Image</Label>
									<Input type="text" name="image" placeholder="Image link" onChange={this.onChange} className="input-margin" />
								</Col>
							</Row>

								<Button color="dark" block>Add</Button>
							</FormGroup>
						</Form>
						
					</ModalBody>
				</Modal>
			</div>
		);
	}
}

const mapStateToProps = state => ({
	item: state.item
}); 

export default connect(mapStateToProps, {addItem})(ItemModal);